#footer {
  width: 100%;
  height: 100vh;
  background: url(../../public/img/bg.jpg) no-repeat center center/cover;
  overflow: hidden;
  position: relative;
}
#footer .footerText .text_area {
  width: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-40%);
  display: flex;
  justify-content: center;
  align-items: center;
}
#footer .footerText .text_area .text {
  color: #F17413;
  font-size: 1.2rem;
  font-weight: 800;
}
#footer .footerText .text_area_ko {
  width: 100%;
  position: absolute;
  top: 52%;
  transform: translateY(-10%);
  display: flex;
  justify-content: center;
  align-items: center;
}
#footer .footerText .text_area_ko .ko {
  color: #fff;
  font-size: 2rem;
  font-weight: 200;
}

@media screen and (max-width: 1280px) {
  #footer {
    height: 770px;
  }
}
@media screen and (max-width: 770px) {
  #footer {
    height: 580px;
  }
  #footer .footerText .text_area_ko {
    top: 53%;
  }
  #footer .footerText .text_area_ko .ko {
    font-size: 1.625rem;
    font-weight: 300;
  }
}
@media screen and (max-width: 550px) {
  #footer .footerText .text_area .text {
    font-size: 1rem;
  }
  #footer .footerText .text_area_ko {
    top: 53%;
  }
  #footer .footerText .text_area_ko .ko {
    font-size: 1.25rem;
  }
}/*# sourceMappingURL=footer.css.map */